const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const {
	RawHTML,
	Fragment,
} = wp.element;
const {
	InspectorControls,
} = wp.blockEditor;
const {
	PanelBody,
	CheckboxControl,
} = wp.components;

const MIUsage = monsterinsights_usage_table_block_object;

const getData = async ( action ) => {

	const URL = `${ MIUsage.ajax_url }?action=${ action }&nonce=${ MIUsage.nonce }`;

	const request = await fetch( URL, {
		method: "GET",
	});

	if ( request.status !== 200 ) {
		throw new Error( __( 'Something went wrong. Could not fetch new data.', 'monsterinsights-usage' ) );
	}
	return request.json();

};

const showCol = ( col, setting ) => {
	
	if ( typeof col === 'string' ) {
		const keys = [ "id", "fname", "lname", "email", "date" ];
		col = keys.indexOf( col );
	}
	
	return setting[col] === true;
}


registerBlockType( 'monsterinsights-usage/table-block', {
	title: __( 'MonsterInsights Usage Table', 'monsterinsights-usage' ),
	description: __( 'A simple block that shows takes MonsterInsights usage stats from an API and shows them in a table', 'monsterinsights-usage' ),
	icon: 'editor-table',
	category: 'widgets',
	keywords: [
		__( 'MonsterInsights', 'monsterinsights-usage' ),
		__( 'Usage', 'monsterinsights-usage' ),
		__( 'Table', 'monsterinsights-usage' ),
		__( 'API', 'monsterinsights-usage' ),
	],

	supports: {
		multiple: false
	},

	attributes: {
		APIData: {
			type: 'object',
		},
		showTitle: {
			type: 'boolean',
		},
		showColID: {
			type: 'boolean',
		},
		showColFirstName: {
			type: 'boolean',
		},
		showColLastName: {
			type: 'boolean',
		},
		showColEmail: {
			type: 'boolean',
		},
		showColDate: {
			type: 'boolean',
		},
	},

	edit( { attributes, setAttributes, className } ) {

		if ( ! attributes.APIData ) {
			getData( 'monsterinsights_usage_api_data' ).then( ( data ) => {
				setAttributes( { APIData: data } );
			});
		}

		const {
			showTitle = true,
			showColID = false,
			showColFirstName = true,
			showColLastName = true,
			showColEmail = true,
			showColDate = true,
		} = attributes;

		const colSettings = [
			showColID,
			showColFirstName,
			showColLastName,
			showColEmail,
			showColDate,
		]

		return (
			<Fragment>
				<InspectorControls>

					<PanelBody
						title={ __( 'Settings', 'monsterinsights-usage' ) }
						initialOpen={ true }
					>
						<CheckboxControl
							label={ __( 'Show Title?', 'monsterinsights-usage' ) }
							checked={ showTitle }
							onChange={ ( isChecked ) => setAttributes( { showTitle: isChecked } ) }
						/>
					</PanelBody>

					<PanelBody
						title={ __( 'Columns', 'monsterinsights-usage' ) }
						initialOpen={ false }
					>
						<CheckboxControl
							label={ __( 'ID', 'monsterinsights-usage' ) }
							checked={ showColID }
							onChange={ ( isChecked ) => setAttributes( { showColID: isChecked } ) }
						/>
						<CheckboxControl
							label={ __( 'First Name', 'monsterinsights-usage' ) }
							checked={ showColFirstName }
							onChange={ ( isChecked ) => setAttributes( { showColFirstName: isChecked } ) }
						/>
						<CheckboxControl
							label={ __( 'Last Name', 'monsterinsights-usage' ) }
							checked={ showColLastName }
							onChange={ ( isChecked ) => setAttributes( { showColLastName: isChecked } ) }
						/>
						<CheckboxControl
							label={ __( 'Email', 'monsterinsights-usage' ) }
							checked={ showColEmail }
							onChange={ ( isChecked ) => setAttributes( { showColEmail: isChecked } ) }
						/>
						<CheckboxControl
							label={ __( 'Date', 'monsterinsights-usage' ) }
							checked={ showColDate }
							onChange={ ( isChecked ) => setAttributes( { showColDate: isChecked } ) }
						/>
					</PanelBody>
				</InspectorControls>
				<div className={ className }>
					{
						attributes.APIData ?
							<>
								{ showTitle ? <h3 className="monsterinsights-usage-title">{ attributes.APIData.title }</h3> : null }
								<div class="wp-block-table">
									<table className="monsterinsights-usage-table">
										<thead>
											<tr>
												{ Object.keys( attributes.APIData.data.headers ).map( ( th, index ) => {
													return showCol( index, colSettings ) ? <th>{ attributes.APIData.data.headers[index] }</th> : '';
												}) }
											</tr>
										</thead>
										<tbody>
											{ Object.keys( attributes.APIData.data.rows ).map( ( row ) => {
												return <tr>
													{ Object.keys( attributes.APIData.data.rows[row] ).map( col => {
														return showCol( col, colSettings ) ? <td>{ attributes.APIData.data.rows[row][col] }</td> : '';
													} ) }
												</tr>
											} ) }
										</tbody>
									</table>
								</div>
							</>
						:
							__( 'Loading...', 'monsterinsights-usage' )
					}
				</div>
			</Fragment>
		);
	},

	save( { attributes } ) {
		const {
			showTitle = true,
			showColID = false,
			showColFirstName = true,
			showColLastName = true,
			showColEmail = true,
			showColDate = true,
		} = attributes;

		// Generate shortcode attributes.
		const myShortcode = '[mi-usage-table show_title="' + showTitle + '" show_col_id="' + showColID + '" show_col_first_name="' + showColFirstName + '" show_col_last_name="' + showColLastName + '" show_col_email="' + showColEmail + '" show_col_date="' + showColDate + '"]';

		return (
			<div>
				<RawHTML>{ myShortcode }</RawHTML>
			</div>
		);
	},
} );
